function makeId(){
  let ID = "";
  let characters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
  for ( var i = 0; i < 12; i++ ) {
    ID += characters.charAt(Math.floor(Math.random() * 36));
  }
  return ID;
}
class Publication extends React.Component {
   constructor() {
      super()
      this.state = {
         liked:false,
         like_count:0,
         date_of_publication:"",
         display_comments:false,
         comment_counter:0
      }
    }
    componentDidMount() {
      var dt = new Date(this.props.post.date_of_publication)

      const year = dt.getFullYear();
      const month = dt.getMonth()+1
      const day = dt.getDate();
      const hour = dt.getHours();
      const minute = dt.getMinutes();

      dt = [year, month, day].join("/") +"  "+ [hour, minute].join(":")

      this.setState({ like_count:this.props.post.like_count });
      this.setState({ liked:this.props.post.liked_by_user });
      this.setState({ date_of_publication:dt });


    }
    displayComments(){
      this.setState({ display_comments:!this.state.display_comments });

    }

    async handleLikeClick() {
      const csrftoken = getCookie('csrftoken');
      // updates like state
      var like_state = !this.state.liked;

      const request = new Request(
        API_URL+"/actions/like/",
        {withCredentials: true,headers: {'X-CSRFToken': csrftoken}}
      );

      fetch(request, {
        method: 'POST',
        credentials: 'include',
        body: JSON.stringify({
          csrfmiddlewaretoken:getCookie('csrftoken'),
          id: this.props.post.id,
          value:like_state
    }),
      })

      this.setState({ liked:like_state });

      // updates like counter
      var like_count = this.state.like_count;
      if(like_state){
        like_count = like_count+1
      } else{
        like_count = like_count-1

      }
      this.setState({ like_count:like_count });
    }
    render() {
      return(
      <div className='post'>
        {Object.keys(this.props.post.media).length > 0 &&
        <div className='publication-media'>
            <img alt ="" className="publication-image" src={API_URL +"/image/"+ this.props.post.media['0']  } />
        </div>}
      </div>
    )}
}

class App extends React.Component {
   constructor() {
      super()
      this.state = {
         items: [],
         hasMore: true,
         following: [],
         pagination_counter:0,
         loaded: true,
      }
      this.fetchData()
   }

     async token ()  {

      axios('http://localhost:8000/security/gettoken/', {
        method: 'GET'
        })
      // console.log(csrftoken)
}

   update_paginations = () =>{
      this.setState({pagination_counter: this.state.pagination_counter + 1 })
   }
   componentDidMount() {
      
      this.token()
   }


   fetchData = () =>  {
      try {
        if(this.state.loaded){
          
          console.log(this.state.pagination_counter)
          axios.get(API_URL+'/api/todos/', {
          params: {
                user:'edua009',
                pagination: this.state.pagination_counter,
            },
          })
          .then(reponse   => reponse.data)
        .then(newItems  => this.setState({ items: [...this.state.items, ...newItems] }))
        .then(res => this.update_paginations())

      }
      } catch (e) {
        console.log(e);
      }
   }

    render() {
      return (
         <div className="publications-container">
            <InfiniteScroll
               dataLength={this.state.items.length}
               next={() => {this.fetchData().then(res => this.setState({ loaded: false })) }}
               // loader={<div class="lds-ellipsis"><div></div><div></div><div></div><div></div></div>}
               hasMore={this.state.hasMore}
               // endMessage={}
               >
               {this.state.items.filter((post) => Object.keys(post.media).length > 0).map((post) => (
                <div className="media-container">
                <article className="post-container">
                  <Publication post={post}></Publication>
                </article>
                </div>
               ))}
            </InfiniteScroll>
         </div>
      )
   }
}
  
export default App